.lineStyle {
    stroke-width: 1.5;
}

.lineStyle:hover {
    stroke-width: 3.5;
}

.introNavButton {
    animation: cssAnimation 0s 30s forwards;
    opacity: 0;
}

@keyframes cssAnimation {
    to {
        opacity: 1;
    }
}

.axisLabelOY {
    font-size: 1.8em;
}

.axisLabelTY {
    font-size: 1.5em;
}

.daySquare:hover {
    stroke-width: 3;
    position: absolute;
    top: 0;
    left: 0;
}